import { InputErrors, InputValues, Transitions } from "../types/types";
import { validateInputs, validateTransition } from "./validation";

export const tokenize = (input: string) => {
    return input.split(',').map(token => token.trim()).filter(token => token.length > 0); 
}

export const tokenizeInputs = (inputs: InputValues) => {
    const result : Record<keyof InputValues, string[]> = {
        states: [],
        initState: [],
        finalStates: [],
        inAlphabet: [],
        auxAlphabet: [],
        initSymbol: [],
        blankSymbol: []
    }

    // Os inputs 5 e 6 estao desnecessariamente sendo tokenizados, porque a forma como o simulador do Rogrigo funciona  que o primeiro caractere
    // desses inputs  considerado e o resto  simplesmente ignorado
    for (const key in inputs)
        result[key as keyof InputValues] = tokenize(inputs[key as keyof InputValues]);
        
    return result;
}