import { TextoErro } from "./styled";
import { useStateContext } from "../../ContextProvider";

const ValidationMessage = () => {
    const {inputStates} = useStateContext();
    const {errors} = inputStates;
    
    return (
        <>
        {(errors.uniqueStates? null : <TextoErro>No pode haver repetio nos estados!</TextoErro>)}
        {(errors.validInitialState? null : <TextoErro>O estado inicial deve pertencer ao conjunto de estados!</TextoErro>)}
        {(errors.validFinalStates? null : <TextoErro>Os estados finais devem pertencer ao conjunto de estados!</TextoErro>)}
        {(errors.uniqueAlphabetSymbols? null : <TextoErro>No pode haver repetio no alfabeto de entrada!</TextoErro>)}
        {(errors.disjointAlphabets? null : <TextoErro>Alfabeto auxiliar e alfabeto de entrada devem ser disjuntos!</TextoErro>)}
        {(errors.alphabetHasStart? null : <TextoErro>Alfabeto de entrada no deve conter smbolo de de incio de fita!</TextoErro>)}
        {(errors.alphabetHasBlank? null : <TextoErro>Alfabeto de entrada no deve conter smbolo de branco!</TextoErro>)}
        {(errors.auxiliaryAlphabetHasStart? null : <TextoErro>Alfabeto auxiliar no deve conter o smbolo de incio de fita!</TextoErro>)}
        {(errors.auxiliaryAlphabetHasBlank? null : <TextoErro>Alfabeto auxiliar no deve conter smbolo de branco!</TextoErro>)}
        </>
    )
}

export default ValidationMessage;